<template>
  <div style="padding-bottom: 20px;">
    <van-nav-bar title="评价" fixed style="height: 46px">
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left"  @click="$toPage(-1)" />
    </van-nav-bar>
    <div style="height: 46px;"></div>
    
    <div class="orderNo">订单号：{{orderNo?orderNo:""}}</div>
    
    <div class="goods_evaluate" v-for="(item,index) in goodList" :key="index">
      <div class="goods_evaluate_0">
        <div class="goods_evaluate_img"><img :src="item.imgURl" /></div>
        <div class="goods_evaluate_star">
          <div class="goods_evaluate_star_box">
            <div class="goods_evaluate_star_box_0">评价等级</div>
            <van-rate v-model="item.level" />
            <div class="goods_evaluate_star_box_1">{{evaluateNum(item.level)}}</div>
          </div>
          <div class="goods_evaluate_star_box">
            <div class="goods_evaluate_star_box_0">描述相符</div>
            <van-rate v-model="item.descmatch" />
            <div class="goods_evaluate_star_box_1">{{evaluateNum(item.descmatch)}}</div>
          </div>
          <div class="goods_evaluate_star_box">
            <div class="goods_evaluate_star_box_0">物流服务</div>
            <van-rate v-model="item.logisticssrv" />
            <div class="goods_evaluate_star_box_1">{{evaluateNum(item.logisticssrv)}}</div>
          </div>
        </div>
      </div>
      <van-field type="textarea" placeholder="商品满足您的期待吗？说说你对他的优点和缺点吧！" rows="4" v-model="item.content" autosize />
    </div>
    
    <div class="submitEvaluateBtn" @click="submitEvaluate">提交评价</div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        goodList:[],
        orderNo:null,
      }
    },
    mounted(){
      this.orderNo = this.$route.query.orderNo;
      this.getGoodsList();
    },
    methods:{
      getGoodsList(){//获取订单商品
        JNB.query({
          serverName:'orderServer'+this.$ser_in(this.$loadUserInfo().comp.storeId),
          cls:'OrderInfoModule',
          method:'getOrderDetail',
          params:[this.$loadUserInfo().comp.storeId,this.orderNo],
          callback: data =>{
            data = JSON.parse(data);
            if(data.code === 200 ){
              let goodList = [];
              data.data.goods[0].goods.forEach(item=>{
                goodList.push({
                  level : 5,
                  descmatch : 5,
                  logisticssrv : 5,
                  content : "",
                  orderno : item.orderno,
                  compid : item.compid,
                  sku : item.pdno,
                  spu : item.spu
                })
              })
              this.$request_img({
                context: this,
                arr: goodList
              })
              this.goodList = goodList;
            }else{
               this.$toast(data.message);
            }
          }
        })
      },
      evaluateNum(value) {
        let text = "";
        switch (value) {
          case 1:
            text = "非常差";
            break;
          case 2:
            text = "差";
            break;
          case 3:
            text = "一般";
            break;
          case 4:
            text = "满意";
            break;
          case 5:
            text = "非常满意";
            break;
        }
        return text;
      },
      submitEvaluate:function(){
        JNB.query({
          serverName:'orderServer'+this.$ser_in(this.$loadUserInfo().comp.storeId),
          cls:'OrderOptModule',
          method:'insertApprise',
          params:{
            orderno:this.orderNo,
            compid:this.$loadUserInfo().comp.storeId,
            appriseArr:this.goodList
          },
          callback: data =>{
            data = JSON.parse(data);
            if(data.code === 200 ){
              this.$router.push({
                path: "/orderList",
                query: {
                  index: 0,
                  canBack: 0
                }
              });
            }else{
              this.$toast.fail(data.message);
            }
          }
        })
      }
    }
  }
</script>

<style scoped>
  .orderNo{
    height: 80px;
    line-height: 80px;
    font-size: 28px;
    color: #999999;
    padding: 0 20px;
  }
  .goods_evaluate{
    background-color: #FFFFFF;
    margin-top: 20px;
  }
  .goods_evaluate_0{
    padding: 20px;
    display: flex;
  }
  .goods_evaluate_img{
    height: 160px;
    width: 160px;
    margin-right: 20px;
  }
  .goods_evaluate_img img{
    height: 160px;
    width: 160px;
    display: block;
  }
  .goods_evaluate_star{
    flex: 1;
    height: 160px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .goods_evaluate_star_box{
    display: flex;
    font-size: 28px;
  }
  .goods_evaluate_star_box_0{
    margin-right: 4px;
  }
  .goods_evaluate_star_box_1{
    margin-left: 4px;
  }
  .submitEvaluateBtn{
    margin: 40px 40px 0;
    height: 100px;
    line-height: 100px;
    text-align: center;
    color: #FFFFFF;
    font-size: 28px;
    border-radius: 15px;
    background-color: #f44;
  }
</style>
